.rich-editor-wrapper {
  width: 100%;
  height: 100%;
}
.rich-editor-wrapper .rich-editor-upload-img {
  text-align: center;
}
.rich-editor-wrapper .rich-text-editor {
  min-height: 200px;
}
.rich-editor-wrapper .ql-editor {
  min-height: 120px;
  padding-left: 10px;
}
.rich-editor-wrapper .ql-editor.ql-blank::before {
  color: #C8C8C8;
  font-style: normal;
  left: 10px;
}
.rich-editor-wrapper .ql-toolbar.ql-snow {
  border: none;
  padding: 10px 0;
  background-color: #F7F7F7;
  border-radius: 2px;
  line-height: 20px;
}
.rich-editor-wrapper .ql-toolbar.ql-snow + .ql-container.ql-snow {
  border: none;
}
.rich-editor-wrapper .ql-toolbar.ql-snow .ql-picker-label {
  outline: none;
}
